<style>
  @font-face {
    font-family:"Alex Brush";
    src: url("https://api.dmg-software.de/api/getFontPath/directory/2/templateId/398/image/s99q1jqja-gyn2de52b-Alex Brush.ttf") format("truetype");
  }  @font-face {
    font-family:"Amazon";
    src: url("https://api.dmg-software.de/api/getFontPath/directory/2/templateId/398/image/dtv2pdov8-p1m9niq92-Amazon.ttf") format("truetype");
  }  @font-face {
    font-family:"Bankgthd";
    src: url("https://api.dmg-software.de/api/getFontPath/directory/2/templateId/398/image/urtv9xj8l-2bnc10tox-Bankgthd.ttf") format("truetype");
  }  @font-face {
    font-family:"CARGTS";
    src: url("https://api.dmg-software.de/api/getFontPath/directory/2/templateId/398/image/u3nxg1j0d-mz5rk9s8u-CARGTS.TTF") format("truetype");
  }  @font-face {
    font-family:"Channel Slanted";
    src: url("https://api.dmg-software.de/api/getFontPath/directory/2/templateId/398/image/0a1xsc188-m9znc80gm-Channel Slanted.ttf") format("truetype");
  }  @font-face {
    font-family:"Comis Sans standard";
    src: url("https://api.dmg-software.de/api/getFontPath/directory/2/templateId/398/image/1e86qie7r-tuzt7bk8w-Comis Sans standard.ttf") format("truetype");
  }  @font-face {
    font-family:"Cooper Black";
    src: url("https://api.dmg-software.de/api/getFontPath/directory/2/templateId/398/image/0x99dtzma-wl2axxswf-Cooper Black.TTF") format("truetype");
  }
  .fa-times:before {
    font-size: 20px;
  }
  .fa-info-circle:before {
    font-size: 18px;
  }
  .dmg-info-symbol {
    display: inline-block;
    vertical-align: middle;
  }
  .dmg-closeProductPopupClass {
    text-align: right;
    padding-bottom: 0;
    float: right;
  }
  .dmg-info-box-container {
    display: none;
    position: absolute;
    z-index: 5000;
    top: 100px;
    left: 0;
    max-width: 600px;
    width: 100%;
    background: #fff;
    padding: 15px;
    border: none !important;
    box-shadow: 0 2px 5px 0 rgba(0,0,0,0.16), 0 2px 10px 0 rgba(0,0,0,0.12);
  }
  .info-box-content {
    padding:10px;
    max-height: 600px;
    overflow-y: scroll;
  }
  .item_description {
    padding-top: 4px;
    padding-bottom: 6px;
    color: #333333;
    font-size: 15px;
    font-weight: 400;
  }
  .dmg-align-button {
    text-align: right;
    #margin-right: 16px;
    padding: 0px;
    margin-bottom: 0px;
  }
  .dmgFielUpload input {
    display: none;
  }
  .dmgFielUpload {
    background-color: #fff !important;
    border: 1px solid #1a7081;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 10px;
  }
  .dmgFielUpload label {
    position: relative;
    display: inline-block;
    width: 100%;
    min-height: 36px;
  }
  .dmgFielUpload label:after {
    content: "Datei hochladen";
    position: absolute;
    right:0;
    background-color: #1a7081;
    font-family: Arial;
    font-size: 17px;
    color: #fff;
    border: 1px solid #1a7081;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    border-radius: 5px;
    padding: 4px 30px;
    cursor: pointer;
  }
  .dmgFielUpload input {
    position: relative;
    display: none;
    left: -90px;
    width: 100%;
    text-indent: -9999px;
    margin-top: 0 !important;
    outline: none !important;
  }

  .tooltip {
    position: relative;
    display: inline-block;
  }

  .tooltip .tooltip-text {
    visibility: hidden;
    width: 320px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    position: absolute;
    z-index: 699 !important;
    top: -5px;
    left: 110%;
  }

  .tooltip .tooltip-text::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 100%;
    margin-top: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent black transparent transparent;
  }

  .tooltip:hover .tooltip-text {
    visibility: visible;
  }

  .dmg_accordion_container {
    z-index: 200 !important;
    display: none;
  }

  .dmg_accordion_container.deactivate {
    display: block;
  }
  .nextStepInMenu {
    background-color: #1a7081;
    color:#fff;
    padding: 4px;
  }
  .dmg_accordion {
    background-color: #f3f3f3;
    color: #000;
    cursor: pointer;
    height: 35px;
    border-bottom: 1px solid #fff !important;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-size: 15px;
    padding-left: 8px;
    padding-right: 8px;
    transition: 0.4s;
    z-index: 200 !important;
  }
  .active, .dmg_accordion:hover {
    background-color: #1a7081;
    color: #fff;
  }
  .dmg_panel {
    padding-top: 6px;
    padding-left: 12px;
    padding-bottom: 6px;
    display: none;
    background-color: white;
    overflow: hidden;
  }
  .dmg_accordion:after {
    content: '\002B';
    color: #000;
    font-weight: bold;
    float: right;
    margin-left: 5px;
  }
  .active:after {
    content: "\2212";
  }
  .icon-dmg-info {
    color: #1a7081;
  }
  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  body {
    #background-color: #0e102f;
  }

  .dmg-container {
    max-width: 1040px;
    margin: 0 auto;
    #background-color: #0e102f;
  }

  .dmg-tab {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 20px 0;
  }

  .dmg-tablinks {
    #background: #0e102f;
    border: 1px solid #1a7081;
    color: #000;
    padding: 15px 50px;
    cursor: pointer;
    border-radius: 50px;
    margin-right: 10px;
    margin-bottom: 10px;
  }

  .centered {
    position: absolute;
    font-family: Amazon;
    color: white;
    font-size: 19px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .dmg-tablinks:last-child {
    margin-right: 0;
  }

  .dmg-tablinks.active, .dmg-tablinks:hover {
    background: #1a7081;
  }

  .tabcontent {
    display: none;
  }

  .dmg-wrap {
    display: flex;
    flex-direction: row;
  }

  .dmg-main {
    flex-basis: 100%;
    #margin-right: 5px;
    #padding: 0px;
    #border: 0px solid #1a7081;;
    #border-radius: 5px;
    #background-color: #090a21;
  }

  .dmg-tab-list {
    padding-bottom: 0px;
    color: #000;
  }

  .dmg-tab-input, .dmg-tab-radio {
    #display: flex;
    flex-direction: row;
    align-items: center;
  }

  .dmg-propertyName {
    color: #1a7081;
    font-size: 13px;
  }

  .minmaxarea {
    color: #1a7081;
  }

  .dmg-tab-input span, .dmg-tab-input label, .dmg-tab-radio .dmg-propertyName {
    min-width: 140px;
    position: relative;
  }

  .dmg-tab-input input, .dmg-tab-input select {
    #padding: 10px;
    min-height: 35px;
    border-radius: 5px;
    border: 0;
    outline: 0;
    width: 100%;
    max-width: 240px;
    text-align: center;
    border: 1px solid #1a7081;
    margin-bottom: 1px;
  }

  .dmg-tab-input select {
    padding-left:4px;
    border: 1px solid #1a7081;;
    color: #1a7081;
    text-align-last: center;
    -webkit-appearance:none;
  }

  .dmg-tab-radio input {
    display: none;
  }

  .dmg-tab-radio .radio-toolbar {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
  }
  .beschichtung-dmg-tab-radio{
   display: flex;
   flex-wrap: wrap;
   flex-direction: row;
  }

  .dmg-tab-radio input + label {
    padding: 4px;
    border: 1px solid #1a7081;;
    cursor: pointer;
    min-width: 70px;
    display: block;
    text-align: center;
    margin-top: 3px;
    margin-bottom: 3px;
    margin-right: 10px;
    border-radius: 5px;
    color: #1a7081;
    width: 125px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .dmg-tab-radio input:checked + label {
    font-weight: 700;
    background: #1a7081;
    color: #fff;
    #border: 4px solid #1a7081;
  }

  .dmg-tab-radio input:focus + label {
    border: 1px dotted #000;
  }

  .dmg-tab-image-list input {
    display: none;
  }

  .dmg-tab-image-list .imagelist-toolbar {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
  }

  .dmg-tab-image-list input + label {
    padding: 4px;
    border: 1px solid #1a7081;;
    cursor: pointer;
    min-width: 70px;
    display: block;
    text-align: center;
    margin-top: 3px;
    margin-bottom: 3px;
    margin-right: 10px;
    border-radius: 5px;
    color: #1a7081;
  }

  .imagelist-toolbar input:checked + label {
    font-weight: 700;
    background: #1a7081;
    color: #fff;
    #border: 4px solid #1a7081;
  }

  .dmg-tab-image-list input:focus + label {
    border: 1px dotted #000;
  }

  .dmg-sidebar {
    flex-basis: 25%;
    padding: 20px;
    border: 1px solid #1a7081;
    border-radius: 5px;
    color: #000;
    background-color: #fff;
  }

  .dmg-preview {
    margin-bottom: 20px;
  }

  .dmg-sidebar-list {
    margin: 15px 0;
    text-align: center;
    color: #000;
  }

  .dmg-sidebar-list h2 {
    padding-bottom: 10px;
    margin-bottom: 20px;
    position: relative;
  }

  .dmg-sidebar-list h2:after {
    content: " ";
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 38px;
    height: 3px;
    background: #1a7081;
  }

  .dmg-sidebar-detail {
    border-bottom: 1px solid #1a7081;
    padding: 2px 0;
    margin-bottom: 10px;
    font-size: 14px;
  }

  .dmg-sidebar-price {
    color: #000;
    text-align: center;
    font-size: 22px;
    display: none;
    #display: block;
  }

  .dmg-tab-buttons {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
  }

  .dmg-tab-buttons .dmg-button {
    margin: 10px;
    background: #1a7081;
    border: 1px solid #1a7081;
    color: #000;
    padding: 15px 50px;
    cursor: pointer;
    font-weight: bold;
    border-radius: 50px;
  }

  .dmg-tab-buttons input:disabled {
    opacity: .7;
  }
  .beschichtung-dmg-tab-radio input + label {
    padding: 4px;
    border: 1px solid #1a7081;
    cursor: pointer;
    min-width: 70px;
    display: block;
    text-align: center;
    margin-top: 3px;
    margin-bottom: 3px;
    margin-right: 10px;
    border-radius: 5px;
    color: #1a7081;
    width: 183px;
    min-height: 47px;
    display: flex;
    align-items: center;
  }
  /*
    .dmg-tab-radio .dmg-propertyName:hover .tooltip {
      display: block;
      position: absolute;
      background: #000;
      color: #000;
      padding: 5px;
      top: -35px;
      left: 10px;
      cursor: pointer;
    }*/

  .preview_fell {
    position: relative;
    width: 258px;
    height: 300px;
    padding: 0 !important;
  }

  .thumbnail {
    display: block;
    padding: 4px;
    margin-bottom: 23px;
    line-height: 1.55;
    background-color: #000;
    border: 1px solid #ddd;
    border-radius: 5px;
    -webkit-transition: border .2s ease-in-out;
    -o-transition: border .2s ease-in-out;
    transition: border .2s ease-in-out;
  }

  #preview_relining, #preview_slackline {
    height: 300px;
    margin: 0 auto;
    position: relative;
  }

  .panel-body *:last-child {
    margin-bottom: 0;
  }

  #preview_slackline {
    padding: 0 3px !important;
  }

  #preview_edging {
    background-position: top center;
    background-repeat: no-repeat;
    height: 300px;
    margin: 0 auto;
    position: relative;
  }

  #preview #bestickung_wrapper {
    height: 300px;
    margin: 0 auto;
    position: relative;
    max-width: 80px;
  }

  #preview_relining, #preview_slackline {
    height: 300px;
    margin: 0 auto;
    position: relative;
  }

  #preview_relining.thumbnail, #preview_slackline.thumbnail {
    background: 0 none;
  }

  #preview_wrapper .thumbnail, #preview_wrapper_halstuch .thumbnail {
    border: 0 none;
    border-radius: 0;
    padding: 0;
  }

  .selected, .material_link:hover.selected, .material_link:active, .selected_button, #configurator .nav-pills li.active a {
    border-color: #f48301;
    outline: 0;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(244, 131, 1, 1);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075), 0 0 8px rgba(244, 131, 1, 1);
  }

  .thumbnail > img, .thumbnail a > img, .carousel-inner > .item > img, .carousel-inner > .item > a > img {
    display: block;
    max-width: 100%;
    height: auto;
  }

  .thumbnail > img, .thumbnail a > img {
    margin-right: auto;
    margin-left: auto;
  }

  @media screen and (max-width: 920px) {
    .dmg-wrap {
      flex-wrap: wrap;
    }

    .dmg-main, .dmg-sidebar {
      flex-basis: 100%;
      margin: 15px;
    }

    .dmg-sidebar {
      order: -1;
    }

    .preview_fell {
      margin: 0 auto;
    }

    .dmg-tab-input, .dmg-tab-radio {
      flex-wrap: wrap;
    }

    .dmg-tab-input span, .dmg-tab-input label, .dmg-tab-radio .dmg-propertyName {
      margin-bottom: 10px;
    }

    .dmg-tab-list .dmg-tab-input-full input {
      width: 100%;
      max-width: 100%;
    }
  }
</style>
<head>
  <meta charset="utf-8">
</head>
<template>
  <div class="dmg-config">
    <div ref="declareJsVariables">
    </div>
    <div ref="insertExpression"></div>
    <div ref="insertMatrix"></div>
    <div class="dmg-wrap">
      <div class="dmg-main">
        <div v-for="(groupName, groupIndex) in allGroups" :ref="groupName.name" class="dmg_accordion_container">
          {{setTabOnHidden(groupName.name)}}
          <button class="dmg_accordion" @click="setOnInactive(groupName.name)">{{groupName.name}}</button>
          <div class="dmg_panel">
            <!-- Tab content -->
            <div :id="groupName.name" class="panel-container">
              <div class="dmg-tab-list" v-for="(property, propertyKey) in propertyGroup[groupName.name]">
                <div v-if="isObject(propertyValue[findIndexByID(property.uniqueId)])">
                  <div class="dmg-tab-input dmg-tab-input-full"
                       v-if="propertyValue[findIndexByID(property.uniqueId)].data_type == 'textField' && propertyValue[findIndexByID(property.uniqueId)] && getAwInputValues(property.uniqueId)">
                    {{setToStringVariable(propertyValue[findIndexByID(property.uniqueId)])}}
                    {{setTabOnDisplay(groupName.name)}}
                    {{executeExpressionForEachElement(propertyValue[findIndexByID(property.uniqueId)].expression, propertyValue[findIndexByID(property.uniqueId)].name, property.uniqueId)}}
                    <div class="dmg-info-symbol" v-if="propertyValue[findIndexByID(property.uniqueId)].short_description.length"><i class="fa fa-info-circle icon-dmg-info" @click="openInfoLightBox(property.uniqueId)"></i></div>
                    <div :id="`dmg-info-box-${property.uniqueId}`"  class="dmg-info-box-container">
                      <div class="dmg-closeProductPopupClass"><i @click="closeInfoLightBox(property.uniqueId)" class="fas fa-times"></i></div>
                      <div class="info-box-content"  v-html="propertyValue[findIndexByID(property.uniqueId)].short_description"></div>
                    </div>
                    <div class="item_description" v-html="propertyValue[findIndexByID(property.uniqueId)].description"></div>
                    <span class="dmg-propertyName">
                            <span v-if="propertyValue[findIndexByID(property.uniqueId)].mandatory_field == 1">*</span>
                            {{propertyValue[findIndexByID(property.uniqueId)].titel}} <span v-show="propertyValue[findIndexByID(property.uniqueId)].minValue.length >0" class="minmaxarea">
                            <small v-if="propertyValue[findIndexByID(property.uniqueId)].maxValue >0">(min {{propertyValue[findIndexByID(property.uniqueId)].minValue}} {{propertyValue[findIndexByID(property.uniqueId)].unitValue}}</small></span>
                            <span v-show="propertyValue[findIndexByID(property.uniqueId)].maxValue >0" class="minmaxarea"> - <small>max {{propertyValue[findIndexByID(property.uniqueId)].maxValue}} {{propertyValue[findIndexByID(property.uniqueId)].unitValue}})</small></span>
                    </span><br>
                    <input type="text" class="dmg-input" :id="propertyValue[findIndexByID(property.uniqueId)].name" v-model="propertyValue[findIndexByID(property.uniqueId)].value"  @input="roundEntry(property.uniqueId)">
                    <div class="item-error" v-if="messageWorning[findMessageWorningByUniqueId(property.uniqueId)]" style="color: #FF0000;">
                      <small>{{messageWorning[findMessageWorningByUniqueId(property.uniqueId)].msg}}</small>
                    </div>
                    <div class="item-error" v-if="checkRemoveManatoryFields(property.uniqueId) "  style="color: #FF0000;">
                      <small>{{messageWorningMandatory[findMessageWorningMandatoryByUniqueId(property.uniqueId)].msg}}</small>
                    </div>
                    <!--    <div v-html="propertyValue[findIndexByID(property.uniqueId)].description"></div>-->
                  </div>
                  <div v-else-if="propertyValue[findIndexByID(property.uniqueId)].data_type == 'matrix'">
                    {{addMatrices(propertyValue[findIndexByID(property.uniqueId)])}}
                    {{setTabOnDisplay(groupName.name)}}
                  </div>
                  <div class="dmg-tab-input"
                       v-else-if="propertyValue[findIndexByID(property.uniqueId)].data_type == 'selectBox' && propertyValue[findIndexByID(property.uniqueId)]">
                    <label>
                      {{setToStringVariable(propertyValue[findIndexByID(property.uniqueId)])}}
                      <span v-if="propertyValue[findIndexByID(property.uniqueId)].mandatory_field == 1">*</span>
                      <span class="dmg-propertyName">
                             {{propertyValue[findIndexByID(property.uniqueId)].titel}}
                      </span>
                      <div class="dmg-info-symbol" v-if="propertyValue[findIndexByID(property.uniqueId)].short_description.length"><i class="fa fa-info-circle icon-dmg-info" @click="openInfoLightBox(property.uniqueId)"></i></div>
                      <div :id="`dmg-info-box-${property.uniqueId}`"  class="dmg-info-box-container">
                        <div class="dmg-closeProductPopupClass"><i @click="closeInfoLightBox(property.uniqueId)" class="fas fa-times"></i></div>
                        <div class="info-box-content"  v-html="propertyValue[findIndexByID(property.uniqueId)].short_description"></div>
                      </div>
                    </label>
                    <br>
                    <div v-if="checkIfMultiplyField(propertyValue[findIndexByID(property.uniqueId)].multiplyFields)" :set="optionDropdown = getSelectBoxElementRelatedTo(property.uniqueId, propertyValue[findIndexByID(property.uniqueId)].name, propertyValue[findIndexByID(property.uniqueId)].multiplyFields, propertyValue[findIndexByID(property.uniqueId)].relatedMultiplyFields)">
                      <div v-if="optionDropdown">
                        {{setTabOnDisplay(groupName.name)}}
                        {{executeExpressionForEachElement(propertyValue[findIndexByID(property.uniqueId)].expression, propertyValue[findIndexByID(property.uniqueId)].name, property.uniqueId)}}
                        <select v-model="propertyValue[findIndexByID(property.uniqueId)].value" :ref="propertyValue[findIndexByID(property.uniqueId)].name"  @change="setMultiplyFields(property.uniqueId, groupName.name, propertyValue[findIndexByID(property.uniqueId)].multiplyFields)">
                          <option v-for="option in optionDropdown" :value="option.value"
                                  :selected="[ option.title == propertyValue[findIndexByID(property.uniqueId)].defaultValue ? true : false]">
                            {{ option.title }} <!--(+{{ option.price }} {{shopCurrency}})-->
                          </option>
                        </select>
                      </div>
                    </div>
                    <div v-if="!checkIfMultiplyField(propertyValue[findIndexByID(property.uniqueId)].multiplyFields)" :set="optionDropdown = getSelectBoxElement(property.uniqueId)">
                      <div v-if="optionDropdown">
                        {{setTabOnDisplay(groupName.name)}}
                        {{executeExpressionForEachElement(propertyValue[findIndexByID(property.uniqueId)].expression, propertyValue[findIndexByID(property.uniqueId)].name, property.uniqueId)}}
                        <select  v-model="propertyValue[findIndexByID(property.uniqueId)].value">
                          <option v-for="option in optionDropdown" :value="option.value"
                                  :selected="[ option.title == propertyValue[findIndexByID(property.uniqueId)].defaultValue ? true : false]">
                            {{ option.title }} <!--(+{{ option.price }} {{shopCurrency}})-->
                          </option>
                        </select>
                      </div>
                    </div>
                    <div class="item-error" v-if="messageWorning[findMessageWorningByUniqueId(property.uniqueId)]" style="color: #FF0000;">
                      <small>{{messageWorning[findMessageWorningByUniqueId(property.uniqueId)].msg}}</small>
                    </div>

                    <div class="item-error" v-if="checkRemoveManatoryFields(property.uniqueId) "  style="color: #FF0000;">
                      <small>{{messageWorningMandatory[findMessageWorningMandatoryByUniqueId(property.uniqueId)].msg}}</small>
                    </div>
                    <!-- <div v-html="propertyValue[findIndexByID(property.uniqueId)].description"></div>-->
                  </div>
                  <div class="dmg-tab-input"
                       v-else-if="propertyValue[findIndexByID(property.uniqueId)].data_type == 'font' && propertyValue[findIndexByID(property.uniqueId)]">
                    {{setCurrentFont(propertyValue[findIndexByID(property.uniqueId)].parentId)}}
                    {{setToStringVariable(propertyValue[findIndexByID(property.uniqueId)])}}
                    {{setTabOnDisplay(groupName.name)}}
                    <label>
                      <span v-if="propertyValue[findIndexByID(property.uniqueId)].mandatory_field == 1">*</span>
                      {{property.name}}
                    </label>

                    <select v-model="propertyValue[findIndexByID(property.uniqueId)].value">
                      <option v-for="option in propertyValue[findIndexByID(property.uniqueId)].fonts" :value="option.value">
                        {{ option.title }}
                      </option>
                    </select>
                    <!--  <div v-html="propertyValue[findIndexByID(property.uniqueId)].description"></div>-->
                  </div>
                  <div class="dmg-tab-input"
