      setMultiplyFields(uniqueId, groupName, dmgFields, storedConfig) {
        let self = this;

        if(dmgFields.length >0) {
          let propertyGroup = {};
          propertyGroup = self.tmpPropertyGroupDuplicate[groupName];
          //if(parseInt(self.propertyValue[self.findIndexByID(uniqueId)].value) == 0 ) {
            let multipliedElement = self.propertyValue[self.findIndexByID(uniqueId)];
            let multipliedElementList = self.propertyValue[self.findIndexByID(multipliedElement.uniqueId)][self.propertyValue[self.findIndexByID(multipliedElement.uniqueId)].data_type];
            dmgFields.forEach(function (fieldPropertyId) {
              for(let i = 1; i < multipliedElementList.length; i++) {
                self.deleteElementInPropertyValue(fieldPropertyId+"_"+multipliedElementList[i].value);
              }
            })
          //}

          for(let i = 1; i <= parseInt(self.propertyValue[self.findIndexByID(uniqueId)].value); i++) {
            dmgFields.forEach(function (fieldPropertyId) {
              if(typeof(fieldPropertyId) == "string") {
                let alreadyItemPositionId = self.findPositionItemByID(fieldPropertyId + '_'+ i, self.configurationJson);
                let alreadyItem = self.configurationJson[alreadyItemPositionId];
                let ObjPropertyValueLocalStr = JSON.stringify(self.findIndexByIDAndColl(fieldPropertyId,self.storeElementMultiplyFields));
                let ObjPropertyValueLocal = JSON.parse(ObjPropertyValueLocalStr);
                ObjPropertyValueLocal.titel = ObjPropertyValueLocal.titel + ' '+ i;
                ObjPropertyValueLocal.expression = ObjPropertyValueLocal.expression;
                ObjPropertyValueLocal.name = ObjPropertyValueLocal.name + i;
                ObjPropertyValueLocal.uniqueId = ObjPropertyValueLocal.uniqueId + '_'+ i;
                if(storedConfig != null) {
                  let alreadyItem = self.findPreconfigItemByIdAndColl(fieldPropertyId + '_'+ i, storedConfig);
                  if(alreadyItem != null) {
                    ObjPropertyValueLocal.value =  alreadyItem.value;
                  }
                } else if(alreadyItem != null) {
                  ObjPropertyValueLocal.value =  alreadyItem.value;
                }
                self.declareAdditionalJSVariables(ObjPropertyValueLocal);
                self.propertyValue.push(ObjPropertyValueLocal);
                [... propertyGroup] = [
                  ...propertyGroup,
                  ...[{ "uniqueId":ObjPropertyValueLocal.uniqueId, "name": ObjPropertyValueLocal.name+"_"+i }]
                ];

                let deletingId = self.findPositionItemByID(fieldPropertyId, propertyGroup);
                if (deletingId != null) {
                  propertyGroup.splice(deletingId, 1);
                }
              }
            })
          }

          this.propertyGroup[groupName] = propertyGroup;
        }
      },
